.card {
  flex: 26% 1;
  float: left;
  padding: 2%;
  width: 100%;
  height: 48%;
  margin: 1%;
  flex-direction: row wrap;
}

.card .menuContent {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.card .menuContent::before, .card .menuContent::after {
  content: '';
  display: table;
}

.card .menuContent::after {
  clear: both;
}

.card .menuContent li {
  display: inline-flex;
}

.card .menuContent li.rightTop .rightTopIcons {
  color: #EDC168;
  color: var(--yellow);
  margin: -12% 5% 0;

}
.rightTopIcons >span {
  display: inline-block;

}
.card .menuContent span {
  position: absolute;
  left: 50%;
  top: 0;
  color: rgba(248,248,248,.8);
  color: var(--white-semi-grey);
  font-size: 1.5em;
  font-weight: 700;
  font-family: 'Bodoni Moda', serif;
  font-family: var(--Chic);
  transform: translate(-50%, 0%);
}

.card .wrapper {
  width: 100%;
  min-height: 100%;
  position: relative;
  overflow: hidden;
  box-shadow: 0 19px 38px rgba(rgb(25,25,25), 0.3), 0 15px 12px rgba(rgb(25,25,25), 0.2);
          box-shadow: 0 19px 38px rgba(var(--grey-very-dark), 0.3), 0 15px 12px rgba(var(--grey-very-dark), 0.2);
}

.card .logoContainer {
  position: relative;
  opacity: 1;
  margin-top: 4em;
  text-align: center;
  align-content: center;
  transition: opacity .3s ease-in-out;
}

.card .wrapper:hover .logoContainer {
  opacity: 0;
  transition: opacity .3s ease-in-out;
}

.card .wrapper:hover .data {
  transform: translateY(0);
}

.card .data {
  position: absolute;
  bottom: 0;
  width: 100%;
  transform: translateY(calc(70px + 1em));
  transition: transform 0.3s ease-in;
}

.card .data .hiddenContent {
  padding: .5em;
  position: relative;
  z-index: 1;
  text-align: center;
}

.card .devName {
  font-size: 1.3em;
  text-align: left;
  -ms-grid-row-align: start;
  align-self: start;
}

.card .projectCardTitle a {
  font-size: 2em;
  font-family: 'Bodoni Moda', serif;
  font-family: var(--Chic);
  font-weight: 300;
}

.card .containerText {
  margin: .2em 0 .5em 0;
  max-height: 5em;
  overflow: scroll;
  color: #EDC168;
  color: var(--yellow);
  padding: .02em;

}

.card .text {
  font-size: .6em;
  font-family: Helvetica, sans-serif;
}

.card input[type='checkbox'] {
  display: none;
}

.card input[type='checkbox']:checked + .menuContent {
  transform: translateY(-60px);
}

.projectCard {
  border: .7px solid #EDC168;
  border: .7px solid var(--yellow);
  cursor: pointer;
  background-color: rgba(25,25,25,.2);
  transition: background 0.3s;
}

.projectCard:hover {
  background-color: rgb(41,41,41);
  background-color: var(--grey-dark);
  transition: background 0.3s;

}

.projectCard .wrapper:hover .menuContent span {
  transform: translate(-50%, -90%);
  opacity: 1;
  font-size: .6em;
  
}

.projectCard .projectHeader {
  color: #EDC168;
  color: var(--yellow);
  padding: 1em;
}

.projectCard .projectHeader::before, .projectCard .projectHeader::after {
  content: '';
  display: table;
}

.projectCard .projectHeader::after {
  clear: both;
}

.projectCard .projectHeader .leftTop {
  float: left;
  font-size: .8em;
  font-family: 'Bodoni Moda', serif;
  font-family: var(--Chic);
}

.projectCard .projectHeader .rightTop {
  float: right;
  font-size: .6em;
}

.projectCard .menuContent {
  float: right;
}

.projectCard .menuContent li {
  position: relative;
}

.projectCard .menuContent span {
  transition: all 0.3s;
  opacity: 0;

}

/* PROJECT DESCRIPTION */
.projectCard .data {
  color: #EDC168;
  color: var(--yellow);
  font-family: 'Bodoni Moda', serif;
  font-family: var(--Chic);
  font-size: 1.5em;
  transform: translateY(calc(70px + 4em));
}

/* PROJECT TITLE */
.projectCard .projectCardTitle p {
  font-size: 1.3em;
  color: #EDC168;
  color: var(--yellow);
  transition: all .3s ease-in-out;
}

/* CHECK IT OUT */
.projectCard .projectButton {
  display: block;
  width: 70%;
  margin: .2em auto 0;
  text-align: center;
  font-size: .6em;
  color: #EDC168;
  color: var(--yellow);
  line-height: 1;
  position: relative;
  font-weight: 700;
  cursor: pointer;
}

.projectCard .projectButton::after {
  content: '\2192';
  opacity: 0;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(0, -50%);
  transition: all 0.3s;
}

.projectCard .projectButton:hover::after {
  transform: translate(5px, -50%);
  opacity: 1;
}
