/* INTEREST HEADINGS ANIMATION */
.Interests-list {
  line-height: 1.2;
  text-transform: uppercase;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* transform: rotateX(-10deg) rotateY(20deg); */
  transform:  rotateY(20deg);

  font-family: 'Lobster';

  font-family: var(--Lobster);
  font-size: 3.5em;
  font-weight: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-font-kerning: normal;
  -webkit-text-size-adjust: 100%;
}

.Interests-list-item {
  position: relative;
  color: transparent;
  cursor: pointer;
  text-shadow: 3px 4px 0px rgb(34,34,34);
  text-shadow: 3px 4px 0px var(--grey-very-dark2);
  /* text-shadow: 2px 1px 0px rgba(255, 255, 255, 0.6); */
}

.Interests-list-item::before {
  content: "";
  display: block;
  position: absolute;
  top: 49%;
  left: -10%;
  right: -10%;
  height: 4px;
  border-radius: 4px;
  margin-top: -2px;
  background: #327E8A;
  /* background: var(--yellow); */
  transform: scale(0);
  transition: transform 0.8s cubic-bezier(0.16, 1.08, 0.38, 0.98);
  z-index: 1;
}

.InterestMask {
  display: block;
  position: absolute;
  overflow: hidden;
  color: #327E8A;
  top: 0;
  height: 49%;
  transition: all 0.8s cubic-bezier(0.16, 1.08, 0.38, 0.98);
}
.InterestMask span {
  display: block;
}

.InterestMask + .InterestMask {
  top: 48.9%;
  height: 51.1%;
}
.InterestMask + .InterestMask span {
  transform: translateY(-49%);
}

.Interests-list-item:hover .InterestMask,
.Interests-list-item:active .InterestMask {
  color: #FFF;
  transform: skewX(12deg) translateX(5px);
}
.Interests-list-item:hover .InterestMask + .InterestMask,
.Interests-list-item:active .InterestMask + .InterestMask {
  transform: skewX(12deg) translateX(-5px);
}
.Interests-list-item:hover::before,
.Interests-list-item:active::before {
  transform: scale(1);
}

