  }
  70% {
    transform: translate3d(0, -60%, 0);
  }
  80% {
    transform: translate3d(0, -70%, 0);
  }
  90% {
    transform: translate3d(0, -80%, 0);
  }
  100% {
    transform: translate3d(0, -90%, 0);
  }
  110% {
    transform: translate3d(0, -100%, 0);
  }
}

